import { get, pollUntilAvailable, post } from './client';
import { JobResponse, PromptPostBody, PromptResponse, Voice } from '../models/models';

const voices = "voices"
const result = "result/"

export const getVoices = () => {
    return get(voices)
}

export const postPrompt = async (prompt: string, selectedVoice: string): Promise<PromptResponse> => {
    const data: PromptPostBody = {
        "prompt": prompt,
        "voice": selectedVoice
    };

    return (await post("submit-prompt", data)).data as PromptResponse;
}

export const postTTS = async (jobId: string) => {
    const endpoint = result + jobId;
    return pollUntilAvailable<JobResponse>(endpoint)
}