// Helper methods for different request types
export const get = (url: string, params = {}) => {
  return apiClient.get(url, { params });
};

export const post = (url: string, data = {}) => {
  return apiClient.post(url, data);
};

export const put = (url: string, data = {}) => {
  return apiClient.put(url, data);
};

export const patch = (url: string, data = {}) => {
  return apiClient.patch(url, data);
};

export const remove = (url: string) => {
  return apiClient.delete(url);
};

const wait = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

export async function pollUntilAvailable<T>(endpoint: string): Promise<T> {
  while (true) {
    const data = await get(endpoint);

    console.log(data.data.status);

    switch (data.data.status) {
        case JobStatus.COMPLETED:
            return data.data;
        case JobStatus.FAILED:
            throw Error(data.data.error);
        case JobStatus.PROCESSING:
            await wait(1000);
            break;
        default:
            throw Error(`Unhandled Job Status: ${data.data.status}`);
      }
  }
}

export default apiClient;