import React, { useState } from 'react';
import {
  Container,
  Typography,
  TextField,
  Button,
  Paper,
  Stepper,
  Step,
  StepLabel,
  Box,
  CircularProgress,
  Alert,
  FormControl, 
  InputLabel, 
  Select, 
  MenuItem, 
  SelectChangeEvent,
} from '@mui/material';
import DownloadIcon from '@mui/icons-material/Download';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PauseIcon from '@mui/icons-material/Pause';
import { getVoices, postPrompt, postTTS } from './api/requests';
import { Voice, UserResponses } from './models/models'

// Define the theme
const theme = createTheme({
  palette: {
    primary: {
      main: '#6573c3',
    },
    secondary: {
      main: '#f9b294',
    },
    background: {
      default: '#f5f5f5',
    },
  },
  typography: {
    fontFamily: "'Poppins', sans-serif",
    h4: {
      fontWeight: 600,
    },
  },
});

// Initial state for user responses
const initialUserResponses: UserResponses = {
  experience: '',
  needs: '',
  negativeSelfTalk: '',
  fears: '',
  tinyShift: '',
};

// Steps for the stepper component
const steps = [
  'Questions',
  'Generate Meditation',
  'Listen',
];

const MeditationApp: React.FC = () => {
  // State hooks
  const [activeStep, setActiveStep] = useState(0);
  const [userResponses, setUserResponses] = useState<UserResponses>(initialUserResponses);
  const [audioUrl, setAudioUrl] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const [isPlaying, setIsPlaying] = useState(false);
  const [audioElement, setAudioElement] = useState<HTMLAudioElement | null>(null);
  const [voices, setVoices] = useState<Voice[]>([]);
  const [selectedVoice, setSelectedVoice] = useState<string | undefined>(undefined);

  // Questions array
  const questions = [
    { id: 'experience', label: 'What do you want to experience in your life?', state: userResponses.experience },
    { id: 'needs', label: 'What do you need?', state: userResponses.needs },
    { id: 'negativeSelfTalk', label: 'What negative self-talk or limiting beliefs do you often struggle with?', state: userResponses.negativeSelfTalk },
    { id: 'fears', label: 'What are you afraid of?', state: userResponses.fears },
    { id: 'tinyShift', label: 'What is one TINY shift you can make now?', state: userResponses.tinyShift },
  ];

  // Handle input changes
  const handleInputChange = (field: keyof UserResponses) => (e: React.ChangeEvent<HTMLInputElement>) => {
    setUserResponses({
      ...userResponses,
      [field]: e.target.value,
    });
  };

  // Navigate to next step
  const handleNext = () => {
    setActiveStep((prevStep) => prevStep + 1);
  };

  // Navigate to previous step
  const handleBack = () => {
    setActiveStep((prevStep) => prevStep - 1);
  };

  // Check if all questions are answered
  const areQuestionsAnswered = (): boolean => {
    return Object.values(userResponses).every(response => response.trim() !== '') && selectedVoice !== undefined;
  };

  // Generate the meditation text
  const generateMeditation = async () => {
    setIsLoading(true);
    setError('');
    
    try {
      const prompt = `
        Create a 2-minute meditation script of positive affirmations based on the following responses:
        
        What they want to experience: ${userResponses.experience}
        What they need: ${userResponses.needs}
        Negative self-talk they struggle with: ${userResponses.negativeSelfTalk}
        What they fear: ${userResponses.fears}
        A tiny shift they can make now: ${userResponses.tinyShift}
        
        Format the meditation as a first-person script with gentle pauses between affirmations. 
        Make it uplifting, compassionate, and tailored to counteract their specific negative self-talk and fears.
        The meditation should be in the style of Gangaji, Rupert Spira, and Eckhart Tolle. 
        Keep it under 400 words.
      `;

      if (selectedVoice === undefined) {
        setError("Voice must be selected before prompt is submitted")
      } else {
        const promptResponse = await postPrompt(prompt, selectedVoice);
        const response = await postTTS(promptResponse.jobId);

        if (response.result != null) {
          const uint8Array = new Uint8Array(response.result);
          const audioBlob = new Blob([uint8Array], { type: 'audio/mp3' });
          const url = URL.createObjectURL(audioBlob);
          setAudioUrl(url);
        
          // Create audio element
          const audio = new Audio(url);
    
          setAudioElement(audio);
          
          // Set up event listeners
          audio.onended = () => {
            setIsPlaying(false);
          };
          
          handleNext();
        }
      }
    } catch (err) {
      setError(`Error: ${err instanceof Error ? err.message : 'Failed to generate meditation'}`);
    } finally {
      setIsLoading(false);
    }
  };

  // Toggle audio playback
  const togglePlay = () => {
    if (!audioElement) return;
    
    if (isPlaying) {
      audioElement.pause();
    } else {
      audioElement.play().then(() => {
        console.log('Audio playing successfully');
      }).catch(error => {
        console.error('Play error details:', error.name, error.message);
        console.error('Audio error state:', audioElement.error);
      });
    }
    
    setIsPlaying(!isPlaying);
  };

  // Reset the form
  const handleReset = () => {
    setUserResponses(initialUserResponses);
    setAudioUrl('');
    setActiveStep(0);
    setError('');
    
    // Clean up audio
    if (audioElement) {
      audioElement.pause();
      setIsPlaying(false);
    }
    if (audioUrl) {
      URL.revokeObjectURL(audioUrl);
      setAudioUrl('');
    }
    setAudioElement(null);
  };

  const handleVoiceChange = (event: SelectChangeEvent<string>) => {
    const voiceId = event.target.value;
    setSelectedVoice(voiceId);
  };

  const handleDownload = () => {
    if (!audioUrl) return;
    
    const link = document.createElement('a');
    link.href = audioUrl;
    link.download = `affirmation-recording-${Date.now()}.mp3`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  if (voices.length === 0 && !isLoading) {
    setIsLoading(true)
    getVoices().then(res => {
      setVoices(res.data);
      setIsLoading(false);
    });
  }

  // Render the appropriate step content
  const getStepContent = (step: number) => {
    switch (step) {
      case 0:
        return (
          <Paper elevation={3} sx={{ p: 4, mt: 4 }}>
            <Typography variant="h5" gutterBottom>Your Journey</Typography>
            {questions.map((q) => (
              <TextField
                key={q.id}
                id={q.id}
                label={q.label}
                variant="outlined"
                fullWidth
                multiline
                rows={3}
                margin="normal"
                value={q.state}
                onChange={handleInputChange(q.id as keyof UserResponses)}
              />
            ))}
            <FormControl fullWidth variant="outlined" margin="normal">
              <InputLabel id="openai-voice-select-label">Voice</InputLabel>
              {isLoading ? (
                <Box display="flex" alignItems="center" justifyContent="center" minHeight={56}>
                  <CircularProgress size={24} />
                </Box>
              ) : (
                <Select
                  labelId="openai-voice-select-label"
                  id="openai-voice-select"
                  value={selectedVoice}
                  onChange={handleVoiceChange}
                  label="Voice"
                >
                  {voices.length > 0 ? voices.map((voice) => (
                    <MenuItem key={voice.key} value={voice.key}>
                      <Box>
                        <Typography variant="body1">{voice.displayName}</Typography>
                      </Box>
                    </MenuItem>
                  )) : null}
                </Select>
              )}
            </FormControl>
          </Paper>
        );
      case 1:
        return (
          <Paper elevation={3} sx={{ p: 4, mt: 4 }}>
            <Typography variant="h5" gutterBottom>Generate Your Meditation</Typography>
            <Typography paragraph>
              We'll create a personalized meditation based on your responses. This will take a moment.
            </Typography>
            {isLoading ? (
              <Box display="flex" justifyContent="center" my={4}>
                <CircularProgress />
              </Box>
            ) : error ? (
              <Alert severity="error" sx={{ mt: 2 }}>{error}</Alert>
            ) : null}
          </Paper>
        );
      case 2:
        return (
          <Paper elevation={3} sx={{ p: 4, mt: 4 }}>
            <Typography variant="h5" gutterBottom>Your Personalized Meditation</Typography>
            <Box display="flex" justifyContent="center" mt={4} gap={2}>
              <Button
                variant="contained"
                startIcon={isPlaying ? <PauseIcon /> : <PlayArrowIcon />}
                onClick={togglePlay}
                disabled={!audioUrl}
                size="large"
              >
                {isPlaying ? 'Pause Meditation' : 'Play Meditation'}
              </Button>
              <Button
                variant="contained"
                color="success"
                startIcon={<DownloadIcon />}
                onClick={handleDownload}
              >
                Download MP3
              </Button>
            </Box>
          </Paper>
        );
      default:
        return 'Unknown step';
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Container maxWidth="md" sx={{ mt: 5, mb: 5 }}>
        <Typography variant="h4" align="center" gutterBottom>
          Affirmation Meditation Generator
        </Typography>
        <Typography variant="subtitle1" align="center" color="text.secondary" paragraph>
          Transform your thoughts into a personalized meditation experience
        </Typography>

        <Stepper activeStep={activeStep} sx={{ pt: 3, pb: 5 }}>
          {steps.map((label) => (
            <Step key={label}>
              <StepLabel>{label}</StepLabel>
            </Step>
          ))}
        </Stepper>

        {getStepContent(activeStep)}

        <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 3 }}>
          <Button
            disabled={activeStep === 0}
            onClick={handleBack}
            variant="outlined"
          >
            Back
          </Button>

          {activeStep === steps.length - 1 ? (
            <Button onClick={handleReset} variant="contained" color="secondary">
              Create New Meditation
            </Button>
          ) : activeStep === 0 ? (
            <Button
              variant="contained"
              onClick={handleNext}
              disabled={!areQuestionsAnswered()}
            >
              Next
            </Button>
          ) : activeStep === 1 ? (
            <Button
              variant="contained"
              onClick={generateMeditation}
              disabled={isLoading}
            >
              Generate Meditation
            </Button>
          ) : null}
        </Box>
      </Container>
    </ThemeProvider>
);
};

export default MeditationApp;